   //*******************Header Site Brand *********************************
   .site-brand {
       font-weight: bold;
       color: #3d2514;
       &__slogan {
           // margin: .5rem;
           @include respond(small-phone) {
               margin: 0;
           }
           @include respond(phone) {
               margin: 0;
           }
           @include respond(tab-port) {
               margin: 0;
           }
       }
   }
   
   //********************************* Header Address Block ****************************8
   .address {
       margin-left: 12rem;
       @include respond(small-phone) {
           margin-left: 11px;
       }
       @include respond(phone) {
           margin-left: 11px;
       }
       &__title {
           font-weight: 600;
           padding: 0.3rem;
           @include respond(small-phone) {
               font-size: .8rem;
               margin-left: 0;
           }
           @include respond(phone) {
               font-size: .9rem;
               margin-left: 0;
           }
           @include respond(tab-port) {
               font-size: .9rem;
               margin-left: 0;
           }
       }
       &__logo {
           max-width: 2rem;
           max-height: 2rem;
       }
   }
   
   //************************************ Header Navbar *************************************
   .navbar_brand--size img {
       width: 2rem;
       height: auto;
       margin-left: 4rem;
       @include respond(small-phone) {
           width: 1.5rem;
           height: auto;
           margin-left: 1rem;
       }
       @include respond(phone) {
           width: 1.5rem;
           height: auto;
           margin-left: 1rem;
       }
       @include respond(tab-port) {
           width: 1.5rem;
           height: auto;
           margin-left: 1rem;
       }
   }
   
   .navbar-nav li a {
       color: black;
       font-weight: bold;
       @include respond(small-phone) {
           margin-left: -10rem;
       }
       @include respond(phone) {
           margin-left: -13rem;
       }
       @include respond(tab-port) {
           margin-left: -13rem;
       }
   }
   
   .navbar-nav li a:hover {
       color: #d90000;
   }
   
   //*************************  Always tasty Button *****************************
   .always-btn {
       a {
           color: var(--color-light-brown);
           text-decoration: none;
           font-weight: 700;
           text-transform: uppercase;
           background-color: #ffc107;
           padding: 1rem;
           transition: all .5s;
           &:link,
           &:visited {
               border-radius: 5px;
           }
           &:hover {
               box-shadow: 0 2rem 2rem rgba(black, 0.15);
               transform: translateY(-3px);
           }
           &:active {
               outline: none;
               box-shadow: 0 .5rem 2rem rgba(black, 0.15);
               transform: translateY(0);
           }
           @include respond(small-phone) {
               font-size: .9rem;
           }
           @include respond(phone) {
               font-size: .9rem;
           }
           @include respond(tab-port) {
               font-size: 1rem;
           }
       }
   }
   
   //****************************** Title  and Descritption  Section *********************************
   .only-typography {
       &__large-title {
           font-size: 3rem;
           color: var(--color-light-brown);
           text-transform: uppercase;
           font-weight: 800;
           @include respond(small-phone) {
               font-size: 1.9rem;
           }
           @include respond(phone) {
               font-size: 2.4rem;
           }
           @include respond(tab-port) {
               font-size: 2.4rem;
           }
       }
   }
   
   //********************* Contact Form Section **************************************
   .web-form {
       .form-text,
       .form-email {
           display: block;
           margin-top: 1em;
           padding: .9em;
           border: 1px solid var(--color-grey-light-4);
           border-radius: 6px;
           border-width: 2px 2px 2px 2px;
           border-color: rgb(230, 230, 230);
           &:focus {
               outline: none;
               border-bottom: 2px solid var(--color-primary-dark-1);
           }
           &:focus:invalid {
               border-bottom: 2px solid var(--color-secondary-dark);
           }
           @include respond(small-phone) {
               max-width: 100%;
           }
           @include respond(phone) {
               max-width: 100%;
           }
           @include respond(tab-port) {
               max-width: 100%;
           }
       }
       &__heading {
           color: var(--color-light-brown);
           font-size: 3rem;
           font-weight: 900;
           @include respond(small-phone) {
               font-size: 1.9rem;
           }
           @include respond(phone) {
               font-size: 2.4rem;
           }
           @include respond(tab-port) {
               font-size: 2.4rem;
           }
       }
       &__small-heading {
           color: var(--color-light-brown);
           font-size: 1.2rem;
       }
       &__find_btn {
           text-align: center;
           display: block;
           margin-top: .8rem;
           padding: .5em;
           border: 2px solid var(--color-grey-light-4);
           border-radius: 6px;
           width: 100%;
           font-weight: 900;
           font-size: 1.4rem;
           transition: all .3s;
           &:link,
           &:visited {
               border-bottom: 1px solid red;
           }
           &:hover {
               box-shadow: 0 1rem 2rem rgba(black, 0.15);
               transform: translateY(-3px);
           }
           &:active {
               outline: none;
               box-shadow: 0 .5rem 2rem rgba(black, 0.15);
               transform: translateY(0);
           }
           @include respond(small-phone) {
               max-width: 100%;
           }
           @include respond(phone) {
               max-width: 100%;
           }
           @include respond(tab-port) {
               max-width: 100%;
           }
       }
   }
   
   //********************* Add to Cart Form Section **************************************
   /* Full card size */
   
   .cart {
       margin: 18px;
   }
   /* Add to cart button */
   
   .button--add-to-cart {
       border-radius: 100px;
       padding: .9rem 4rem;
       border: none;
       background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
       font-size: 1.2rem;
       font-weight: 500;
       color: white;
       transition: all .3s;
       &:hover {
           box-shadow: 0 1rem 2rem rgba(black, 0.15);
           transform: translateY(-3px);
       }
       &:active {
           outline: none;
           text-decoration: none;
           box-shadow: 0 .5rem 2rem rgba(black, 0.15);
           transform: translateY(0);
       }
       &:focus {
           outline: none;
       }
   }
   
   //********************* Add to Cart Table Section **************************************
   .views-table {
       margin: 20px 0px 70px;
       box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
   }
   
   .views-table {
       border-radius: 5px;
       font-size: 15px;
       font-weight: normal;
       border: none;
       border-collapse: collapse;
       width: 100%;
       max-width: 100%;
       white-space: nowrap;
       background-color: white;
   }
   
   .views-table td,
   .views-table th {
       text-align: center;
       padding: 8px;
   }
   
   .views-table td {
       border-right: 1px solid #e6e6e6;
       font-size: 15px;
   }
   
   .views-table thead th {
       color: #ffffff;
       background: #2323238c;
   }
   
   .views-table thead th:nth-child(odd) {
       color: #ffffff;
       background: black;
   }
   
   .views-table tr:nth-child(even) {
       background: #F8F8F8;
   }
   
   .delete-order-item {
       border-radius: 100px;
       padding: 5px 20px;
       border: none;
       background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
       font-size: 1rem;
       font-weight: 100;
       color: white;
       transition: all .3s;
       &:hover {
           background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark));
           box-shadow: 0 1rem 2rem rgba(black, 0.15);
           transform: translate(-3px);
       }
       &:active {
           outline: none;
           text-decoration: none;
           box-shadow: 0 .5rem 2rem rgba(black, 0.15);
           transform: translateY(0);
       }
       &:focus {
           outline: none;
       }
   }
   /* Quantity Number */
   
   .form-number {
       text-align: center;
       text-align: center;
       border-style: hidden;
       cursor: pointer;
   }
   /* update button */
   
   #edit-submit {
       background-image: linear-gradient(to right, #ffa900, #fda014);
       text-transform: uppercase;
       color: white;
       vertical-align: bottom;
       border: 0;
       -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .2), 0 2px 10px 0 rgba(0, 0, 0, .1);
       box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .2), 0 2px 10px 0 rgba(0, 0, 0, .1);
       font-weight: 500;
       padding: .625rem 1.5rem .5rem;
       font-size: .75rem;
       line-height: 1.5;
       transition: all .3s;
       &:hover {
           background-image: linear-gradient(to left, #ffaa00c4, #ffaa00c4);
           box-shadow: 0 1rem 2rem rgba(black, 0.15);
           transform: translateY(-3px);
       }
       &:active {
           outline: none;
           text-decoration: none;
           box-shadow: 0 .5rem 2rem rgba(black, 0.15);
           transform: translateY(0);
       }
   }
   
   #edit-checkout {
       background-image: linear-gradient(to right, #39c0ed, #39c0ed);
       text-transform: uppercase;
       color: white;
       vertical-align: bottom;
       border: 0;
       -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .2), 0 2px 10px 0 rgba(0, 0, 0, .1);
       box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .2), 0 2px 10px 0 rgba(0, 0, 0, .1);
       font-weight: 500;
       padding: .625rem 1.5rem .5rem;
       font-size: .75rem;
       line-height: 1.5;
       transition: all .3s;
       &:hover {
           background-image: linear-gradient(to left, #1268f150, #1268f171);
           box-shadow: 0 1rem 2rem rgba(black, 0.15);
           transform: translateY(-3px);
       }
       &:active {
           outline: none;
           text-decoration: none;
           box-shadow: 0 .5rem 2rem rgba(black, 0.15);
           transform: translateY(0);
       }
   }
   /* Sub Total Money */
   
   .order-total-line__subtotal {
       padding: 0px 13px;
       text-align: right;
       border-bottom: 1px solid #fda113;
   }
   /* Responsive */
   
   @media (max-width: 767px) {
       .views-table {
           display: block;
           width: 100%;
       }
       .views-table:before {
           content: "Scroll horizontally >";
           display: block;
           text-align: right;
           font-size: 11px;
           color: white;
           padding: 0 0 10px;
       }
       .views-table tbody,
       thead th {
           display: block;
       }
       .views-table thead th:last-child {
           border-bottom: none;
       }
       .views-table thead {
           float: left;
       }
       .views-table tbody {
           width: auto;
           position: relative;
           overflow-x: auto;
       }
       .views-table td,
       .views-table th {
           padding: 20px .625em .625em .625em;
           height: 60px;
           vertical-align: middle;
           box-sizing: border-box;
           overflow-x: hidden;
           overflow-y: auto;
           width: 120px;
           font-size: 13px;
           text-overflow: ellipsis;
       }
       .views-table thead th {
           text-align: left;
           border-bottom: 1px solid #f7f7f9;
       }
       .views-table tbody tr {
           display: table-cell;
       }
       .views-table tbody tr:nth-child(odd) {
           background: none;
       }
       .views-table tr:nth-child(even) {
           background: transparent;
       }
       .views-table tr td:nth-child(odd) {
           background: #F8F8F8;
           border-right: 1px solid #E6E4E4;
       }
       .views-table tr td:nth-child(even) {
           border-right: 1px solid #E6E4E4;
       }
       .views-table tbody td {
           display: block;
           text-align: center;
       }
   }