     
     //************** All these classes use in header seciton
      .responsive-p-7 {
                @include respond(tab-port) {
                       padding: 5rem 2rem;
                }
                @include respond(desk-port) {
                    padding: 3rem;
                }
                @include respond(large-port) {
                    padding: 7rem;
                }
                @include respond(largest-port) {
                    padding: 7rem;
                }
            }
            .responsive-mt-2 {
                @include respond(tab-port) {
                    margin-left: 0rem;
                }
                @include respond(desk-port) {
                    margin-left: 2rem;
                }
                @include respond(large-port) {
                    margin-left: -2rem;
                }
                @include respond(largesst-port) {
                    margin-left: -2rem;
                }
            }
            .responsive-pb {
                padding-bottom: 10rem;
                @include respond(phone) {
                    padding-bottom: 14rem;
                }
                @include respond(tab-port) {
                    padding-bottom: 9rem;
                }
                @include respond(desk-port) {
                    padding-bottom: 15rem;
                }
                @include respond(large-port) {
                    padding-bottom: 10rem;
                }
            }




