                    /*breakpoints 

                    _ Phone      0-600px
                    _ tab-port   600-900px
                    _ tab-land    900-1200px
                    _ Normal Css  1200-1800
                    _ big-disktop   1800px +

                    */
                    
                    @mixin respond($breakpoint) {
                        @if $breakpoint==extra-small {
                            // Extra small devices (portrait phones, less than 576px/16 = 35.99)
                            @media (max-width: 30.99em) {
                                @content
                            }
                            ;
                        }
                        @if $breakpoint==small-phone {
                            // Extra small devices (portrait phones, less than 576px/16 = 35.99)
                            @media (max-width: 35.99em) {
                                @content
                            }
                            ;
                        }
                        @if $breakpoint==phone {
                            // Small devices (landscape phones, 576px and up 767.98px)
                            @media (min-width: 36em) and (max-width: 47.99em) {
                                @content
                            }
                            ;
                        }
                
                        @if $breakpoint==tab-port {
                            // Medium devices (tablets, 768px and up 991.98px)
                            @media (min-width: 47em) and (max-width: 61.99em) {
                                @content
                            }
                            ;
                        }
                        @if $breakpoint==desk-port {
                            // Large devices (desktops, 992px and up 1199.98px)
                            @media (min-width: 62em) and (max-width: 74.99em) {
                                @content
                            }
                            ;
                        }
                        @if $breakpoint==large-port {
                            // Extra large devices (large desktops, 1200px and up)
                            @media (min-width: 75em) and (max-width: 90.99em) {
                                @content
                            }
                            ;
                        }
                        @if $breakpoint==largest-port {
                            // Extra large devices (large desktops, 1200px and up)
                            @media (min-width: 76em) {
                                @content
                            }
                            ;
                        }
                    }