//*********************  Header Section ***************************
   .header {
            &__descritpion {
                font-weight: bold;
                color: var(--color-light-brown);
                text-transform: uppercase;
                border: 2.2px solid var(--color-light-brown);
                border-style: dashed;
                padding: 1rem;
                margin-right: 10rem;
                @include respond(small-phone) {
                    font-size: .6rem;
                    border: none;
                    // text-align: center;
                    padding-top: 2rem;
                }
                @include respond(phone) {
                    font-size: .9rem;
                    border: none;
                    // text-align: center;
                    padding-top: 5rem;
                }
                @include respond(phone-tab) {
                    font-size: 1rem;
                    // margin-right: 17rem;
                }
                @include respond(tab-port) {
                    font-size: 1rem;
                    margin-right: 12rem;
                }
                @include respond(desk-port) {
                    // max-width: 100%;
                    font-size: 1rem;
                    margin-right: 6rem;
                }
            }
            &__title {
                color: var(--color-light-brown);
                text-transform: uppercase;
                font-weight: 800;
                font-size: 7rem;
                text-shadow: 2px 2px var( --color-primary-light-red);
                @include respond(extra-small) {
                    font-size: 1rem;
                    margin-right: 0;
                    text-align: center;
                }
                @include respond(small-phone) {
                    font-size: 1.5rem;
                    margin-right: 13rem;
                    text-align: center;
                }
                @include respond(phone) {
                    font-size: 2.4rem;
                    margin-right: 16rem;
                    text-align: center;
                }
                @include respond(tab-port) {
                    font-size: 3.4rem;
                    margin-right: 28rem;
                }
                @include respond(desk-port) {
                    // max-width: 100%;
                    font-size: 5rem;
                    // margin-right: -2rem;
                }
            }
      
        }
        



        //******************* Second Section Scss **************************************
        .second_section {
            &__image {
                position: relative;
            }
            &__full-width img {
                max-width: 100%;
                width: 1650px;
                // height: auto;
                transition: all .5s;
                @include respond(small-phone) {
                    max-width: 100%;
                    width: 1650px;
                    height: auto;
                }
                @include respond(phone) {
                    max-width: 100%;
                    width: 1650px;
                    height: auto;
                }
                @include respond(tab-port) {
                    max-width: 100%;
                    width: 1650px;
                    height: auto;
                }
                @include respond(desk-port) {}
                &:hover {
                    transform: translateY(-.9rem);
                    opacity: .8;
                }
            }
            &__big-title {
                font-size: 2rem;
                font-weight: bold;
                color: var(--color-grey-light-1);
                text-transform: uppercase;
                position: absolute;
                top: 3rem;
                left: 2rem;
                @include respond(small-phone) {
                    font-size: 1.1rem;
                    max-width: 7rem;
                }
                @include respond(phone) {
                    font-size: 1.3rem;
                    max-width: 7rem;
                }
                @include respond(tab-port) {
                    font-size: 1.3rem;
                }
                @include respond(desk-port) {
                    // max-width: 100%;
                    font-size: 2rem;
                    // margin-right: -2rem;
                }
            }
            &__small-title {
                color: var(--color-grey-light-1);
                text-transform: uppercase;
                position: absolute;
                top: 1rem;
                left: 2rem;
                @include respond(small-phone) {
                    font-size: .8rem;
                    top: 1.7rem;
                }
                @include respond(phone) {
                    font-size: 1rem;
                    top: 1.7rem;
                }
                @include respond(tab-port) {
                    font-size: 1.2rem;
                    top: 1.7rem;
                }
                @include respond(desk-port) {
                    font-size: 1.5rem;
                }
            }
            &__font-break {
                max-width: 15rem;
                @include respond(small-phone) {
                    max-width: 7rem;
                }
                @include respond(phone) {
                    max-width: 7rem;
                }
                @include respond(tab-port) {
                    max-width: 7rem;
                }
            }
        }
        
        
        //************************************* Small Burgur in Three parts with buttons  Section  ********************
        .burger {
            display: flex;
            &__image img {
                max-width: 80%;
                height: auto;
                display: block;
                margin-left: auto;
                margin-right: auto;
                transition: all .5s;
                &:hover {
                    transform: scale(1.05) translateY(-.5rem);
                    -webkit-box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
                    -moz-box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
                    box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
                }
            }
            &__image-size img {
                max-width: 63%;
            }
            &__large-text-clr {
                color: var(--color-light-brown);
                font-size: 1.8rem;
                font-weight: 700;
            }
            &__burger__descritption {
                justify-content: space-around;
            }
            &__btn-order a {
                text-transform: uppercase;
                background-color: #bf2222;
                color: #fff;
                text-decoration: none;
                font-weight: 600;
                transition: all .3s;
                &:link,
                &:visited {
                    padding: 1rem 3rem;
                    border-radius: 2px;
                }
                &:hover {
                    box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
                    transform: translateY(-3px);
                }
                &:active {
                    outline: none;
                    box-shadow: 0 .5rem 2rem rgba(black, 0.15);
                    transform: translateY(0);
                }
            }
    
        }
        
        //*********************** Upcomming Events Section******************************************
        .event {
            &__bg-shadow {
                padding: 0 0 1.2rem;
                transition: all .8s ease;
                -webkit-box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
                -moz-box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
                box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
                border-radius: 10px;
            }
            &__image img {
                margin-left: -1rem;
                max-width: 100%;
                height: auto;
                border-radius: 13px;
                transition: all .5s;
                &:hover {
                    transform: scale(1.05) translateY(-.5rem);
                }
                @include respond(tab-port) {
                    margin-top: 3rem;
                }
            }
            &__smal-title {
                margin-bottom: -.4rem;
                color: #3d3d3d;
                text-transform: uppercase;
                font-weight: bold;
                text-transform: uppercase;
            }
            &__large-title {
                color: var(--color-light-brown);
                text-transform: uppercase;
                font-weight: 900;
                font-size: 3rem;
                @include respond(small-phone) {
                    font-size: 1.9rem;
                }
                @include respond(phone) {
                    font-size: 1.8rem;
                }
                @include respond(tab-port) {
                    font-size: 1.8rem;
                }
                @include respond(disk-port) {
                    font-size: 1.9rem;
                }
            }
            &__descritpion {
                word-spacing: 2px;
            }
        }
        
        //*************************** FOOTER Section ******************************************
        .footer {
            color: #fff;
            padding: 9rem 5rem;
            @include respond(small-phone) {
                padding: 5rem .8rem;
            }
            @include respond(phone) {
                padding: 7rem 2rem;
            }
            &__footer-logo {
                display: flex;
            }
            &__footer-logo img {
                max-width: 5rem;
                height: auto;
                @include respond(small-phone) {
                    max-width: 2rem;
                    height: auto;
                }
                @include respond(phone) {
                    max-width: 2rem;
                    height: auto;
                }
                @include respond(tab-port) {
                    max-width: 2rem;
                    height: auto;
                }
            }
            &__large-title {
                font-size: 3rem;
                color: #fff;
                font-weight: 700;
                padding-left: 1rem;
                padding-bottom: 1rem;
                @include respond(small-phone) {
                    font-size: 1.8rem;
                }
                @include respond(phone) {
                    font-size: 2rem;
                }
                @include respond(tab-port) {
                    font-size: 2rem;
                }
            }
            &__location {
                font-weight: 600;
                @include respond(small-phone) {
                       padding-top: 3rem;      
                }
                @include respond(phone) {
                       padding-top: 3rem;
                }

                &-title {
                    display: flex;
                    transition: all .5s;
                @include respond(small-phone) {
                      font-size: .8rem;
                }
                @include respond(phone) {
                       font-size: .8rem;
                }
                @include respond(tab-port) {
                           font-size: .9rem;
                }
                    &:hover,
                    &:active {
                        color: var(--color-orange-light);
                        // box-shadow: 0 1rem 2rem rgba(black, .4);
                        transform: rotate(0deg) scale(1.2);
                    }
                }
                &-email {
                    display: flex;
                    transition: all .5s;
                    &:hover,
                    &:active {
                        color: var(--color-orange-light);
                        // box-shadow: 0 1rem 2rem rgba(black, .4);
                        // transform: rotate(0deg) scale(1.2);
                        transform: skewY(0deg) skewX(15deg) scale(1.1);
                    }
                }
                font-weight: 600;
                transition: all .5s;
            }
            &__location-email a {
                color: currentColor;
                font-weight: 600;
                text-decoration: none;
                transition: all .3s;
                background-color: transparent;
                &:hover {
                    color: var(--color-secondary-light);
                    box-shadow: 0 1rem 3rem rgba(rgb(240, 186, 87), 0.5);
                    transform: scale(-3px);
                }
                &:active {
                    color: rgb(58, 117, 245);
                    box-shadow: 0 .5rem 2rem rgba(rgb(58, 117, 245), 0.15);
                    transform: translateY(0);
                }
            }
            &__copyright {
                font-weight: 600;
                transition: all .5s;
                &:hover,
                &:active {
                    color: var(--color-orange-light);
                    // box-shadow: 0 1rem 2rem rgba(black, .4);
                    transform: rotate(0deg) scale(1.1);
                }
            }
            &__icon {
                 img{
                      transition: all .3s;
                    &:hover,
                    &:active {
                        // box-shadow: 0 1rem 2rem rgba(grey, .4);
                        transform: rotate(0deg) scale(1.2);
                    }
                 }
                
                @include respond(small-phone) {
                       padding-top: 3rem;
                       text-align: center;
                }
                @include respond(phone) {
                       padding-top: 3rem;
                       text-align: center;
                }
                @include respond(tab-port) {
                        padding-top: 3rem;
                          text-align: center;
                }
            }
        }
        
        .margin-left {
            margin-left: 1rem;
        }